body {
  margin: 0px; }

#demo {
  position: absolute;
  width: 100%;
  height: 100%;
  text-align: center; }
  #demo .title {
    margin: 40px;
    font-weight: bold;
    font-size: 32px;
    color: #8e8e8e; }
  #demo .key {
    display: inline-block;
    color: #5e5e5e;
    font-family: arial;
    font-size: 26px;
    text-decoration: none;
    text-align: center;
    margin: 20px 10px;
    padding: 15px 20px;
    background-color: #eff0f2;
    border-radius: 4px;
    border-top: 1px solid #f5f5f5;
    box-shadow: inset 0 0 25px #e8e8e8, 0 1px 0 #c3c3c3, 0 2px 0 #c9c9c9, 0 2px 3px #333;
    text-shadow: 0px 1px 0px #f5f5f5; }
